import React, { Component } from 'react';

export class List extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: []
        }
    }

    renderData(){
        fetch('https://jsonplaceholder.typicode.com/comments')
        .then(res => res.json())
        .then(data => {
            this.setState({
                data: data
            })
        }
        )
    }

    componentDidMount() {
        this.renderData()
    }


    render() {
        return (
            <div className="wrapper">
            <h1>Titel</h1>
            <p>
                laudantium enim quasi est quidem magnam voluptate ipsam eos tempora quo necessitatibus 
                dolor quam autem quasi reiciendis et nam sapiente accusantium laudantium enim quasi est quidem 
                magnam voluptate ipsam eos tempora quo necessitatibus dolor quam autem quasi reiciendis et nam sapiente accusantium
            </p>
            <div className="fetchInfo">
            {this.state.data.map(d => (<div className="testaren" key={d.id}>{d.body}</div>))}
            </div>
            </div>
        )
    }
}
export default List